.time-line {
	background-image: url(/static/media/Background.bac99c5a.svg);
	background-repeat: no-repeat;
	background-size: 100%;
	width: 100%;
	height: 3072px;
	background-position: top;
	background-size: 400px;
}

div {
	align-content: center;
	font-family: 'Nunito', Arial, Roboto;
	margin-bottom: 50px;
}

.title {
	margin-top: 50px;
	font-family: Nunito;
	text-align: center;
	text-transform: uppercase;
}

.title-small {
	font-size: 1em;
}

.title-big {
	font-size: 1.7em;
}

.intro {
	width: 300px;
	text-align: center;
	margin: auto;
	margin-top: 30px;
}

.logo {
	width: 200px;
	height: 200px;
	border-style: solid;
	margin: auto;
}

.text-box {
	width: 1300px;
	height: 290px;
	display: block;
	margin: auto;
}

.left,
.right {
	width: 440px;
	height: 250px;
	border-width: 1px;
	border-style: solid;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 30px;
	padding: 20px;
}

.left {
	float: left;
}

.right {
	float: right;
}

.title {
	text-transform: uppercase;
	font-size: larger;
}

.animation-box {
	width: 1300px;
